}

@media screen and (max-width: 1585px) {
    .header__menu {
        display: flex;
        flex-direction: column;
        padding-top: 100px;
    }

    .header__nav {
        margin-bottom: 30px;
    }

    .header__content {
        margin-top: -50px;
    }

    .about__wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .about__title::after {
        display: none;
    }

    .about__inner {
        margin-bottom: 50px;
    }

    .product__column {
        min-width: 480px;
    }

    .product__wrapper {
        overflow: scroll;
    }

    .story__wrapper::after {
        display: none;
    }

    .story__wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .story__description {
        margin-top: 0;
        margin-left: 20px;
    }

    .story__img {
        margin: 50px;
        padding: 0 20px;
    }

    .testimonials__wrapper::after {
        display: none;
    }

    .testimonials__wrapper::before {
        display: none;
    }

    .testimonials__img:hover {
        transform: none;
    }

    .testimonials__box {
        flex-direction: column-reverse;
    }

    .testimonials__inner {
        margin-right: 0;
    }

    .blog {
        padding: 50px 0;
    }

    .blog__container::after {
        display: none;
    }

    .blog__wrapper {
        flex-direction: column;
        align-items: center;
        margin-left: 20px;
    }

    .blog__box {
        margin-top: 20px;
    }

    .blog__article {
        margin: 0;
    }

    .instagram__img {
        min-width: 384px;
    }

    .instagram__inner {
        overflow: auto;
    }

    .footer__top {
        flex-direction: column;
    }

    .footer__logo {
        margin-top: 0;
        align-self: center;
    }

    .footer__information {
        margin-top: 50px;
        margin-left: 40px;
    }

    .footer__title {
        text-align: center;
    }

    .information__list {
        display: flex;
        justify-content: space-evenly;
    }

    .footer__payment {
        margin-top: 50px;
        margin-left: 0;
        max-width: none;
    }

    .payment__list {
        display: flex;
        justify-content: space-evenly;
    }

    .footer__column {
        margin-top: 50px;
        margin-left: 65px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .footer__social {
